@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/MuseoSansCyrl-300.eot");
  src: local("MuseoSansCyrl-300"), url("../fonts/MuseoSansCyrl-300.eot?#iefix") format("embedded-opentype"), url("../fonts/MuseoSansCyrl-300.woff") format("woff"), url("../fonts/MuseoSansCyrl-300.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/MuseoSansCyrl-500.eot");
  src: local("MuseoSansCyrl-500"), url("../fonts/MuseoSansCyrl-500.eot?#iefix") format("embedded-opentype"), url("../fonts/MuseoSansCyrl-500.woff") format("woff"), url("../fonts/MuseoSansCyrl-500.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/MuseoSansCyrl-700.eot");
  src: local("MuseoSansCyrl-700"), url("../fonts/MuseoSansCyrl-700.eot?#iefix") format("embedded-opentype"), url("../fonts/MuseoSansCyrl-700.woff") format("woff"), url("../fonts/MuseoSansCyrl-700.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
* {
  font-family: "Museo Sans Cyrl", "Helvetica Neue", Arial, sans-serif;
}

.container {
  max-width: 960px;
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.promo {
  min-height: 650px;
  padding: 20px 0px 93px;
  background: #555555 url(../img/header-bg.png) no-repeat center center/cover;
}
.promo__wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 91px;
  color: #fff;
}
.promo__header {
  font-weight: 700;
  font-size: 42px;
  line-height: 51px;
  text-transform: uppercase;
}
.promo__subheader {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  text-transform: uppercase;
}
.promo__icons {
  margin: 60px 20px 65px 0;
  width: 424px;
  display: flex;
  justify-content: space-between;
}
.promo__icon {
  width: 136px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  color: #fff;
}
.promo__icon img {
  display: block;
  margin: 0 auto 20px;
}
.promo__link {
  margin-top: 8px;
  font-weight: 500;
  font-size: 16px;
  color: #fff;
}
.promo__link a {
  color: #fff;
  text-decoration: underline;
}

.button {
  width: 153px;
  height: 32px;
  background-color: #c70101;
  border-radius: 5px;
  border: none;
  font-weight: 500;
  font-size: 12px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
}
.button--promo {
  max-width: 100%;
  width: 303px;
  height: 54px;
  font-size: 16px;
}
.button--submit {
  width: 100%;
  height: 55px;
  font-size: 16px;
}
.button:hover {
  background-color: #be0c0c;
}
.button--mini {
  max-width: 100%;
  height: 40px;
  width: 131px;
  font-size: 14px;
}

.title {
  font-weight: 700;
  font-size: 30px;
  line-height: 100%;
  text-align: center;
  color: #0D0D0D;
  text-transform: uppercase;
}
.title--left {
  text-align: left;
  font-size: 36px;
  line-height: 54px;
  color: #fff;
}

.form {
  margin-top: 36px;
  max-width: 100%;
  width: 300px;
}
.form__row {
  display: flex;
  flex-direction: column;
  margin-bottom: 12px;
}
.form__row:nth-child(3) {
  margin-bottom: 17px;
}
.form input {
  height: 50px;
  padding: 0 10px;
  border: 1px solid #fff;
  border-radius: 5px;
  font-size: 16px;
  border: none;
  transition: all 0.3s;
  border: 1px solid #fff;
}
.form input::placeholder {
  font-weight: 300;
  font-size: 14px;
  color: #4B4B4B;
}
.form input:focus {
  border: 1px solid #c70101;
  outline: none;
  box-shadow: 0 0 5px #c70101b5;
}
.form input:hover {
  border: 1px solid #c70101;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__item {
  flex: 0 0 30%;
}
.header__logo {
  width: 251px;
}
.header__logo img {
  width: 100%;
}
.header__official {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  color: #fff;
}
.header__official span {
  display: block;
  font-size: 18px;
  line-height: 22px;
  margin-top: 8px;
}
.header__contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.header__phone {
  display: block;
  margin-bottom: 4px;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: right;
  color: #fff;
}
.header__phone:hover {
  color: #c7b44c;
  text-decoration: none;
}

.adventeges {
  min-height: 473px;
  background-color: #F6F6F6;
  padding: 73px 0 65px;
}
.adventeges__items {
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.adventeges__item {
  margin-top: 20px;
  width: 25%;
  text-align: center;
}
.adventeges__icon {
  margin-bottom: 35px;
}
.adventeges__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 100%;
  color: #0D0D0D;
  margin-bottom: 20px;
}
.adventeges__desc {
  font-weight: 300;
  font-size: 14px;
  line-height: 18px;
  color: #0D0D0D;
}
.adventeges__desc a {
  text-decoration: underline;
  text-transform: uppercase;
  color: #f00;
}

.consultation {
  min-height: 680px;
  padding: 100px 0 108px;
  background: #555555 url("../img/counselling-bg.png") no-repeat center center/cover;
}
.consultation__descr {
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  color: #fff;
}

.carousel__button-next::after, .carousel__button-prev::after {
  content: "";
}

.carousel {
  padding: 81px 0;
}
.carousel__wrapper {
  align-items: center;
}
.carousel__slide {
  padding: 0 50px;
}
.carousel__slide img {
  display: block;
  width: 750px;
  max-width: 100%;
  margin: 0 auto;
}
.carousel__pagination {
  position: static;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25px;
}
.carousel__button {
  font-size: 50px;
  color: #c70101;
}
.carousel .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  transition: all 0.2s ease-in-out;
}
.carousel .swiper-pagination-bullet-active {
  background: #c70101;
  width: 13px;
  height: 13px;
}

.catalog {
  min-height: 645px;
  padding-bottom: 80px;
  transition: height 2s ease-in-out;
}
.catalog__tabs {
  margin: 45px 0 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}
.catalog__tab {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  width: 220px;
  height: 50px;
  border: 2px solid #D0D0D0;
  line-height: 20px;
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  text-transform: uppercase;
  color: #6B6B6B;
  transition: all 0.4s ease;
  transform: skewX(-25deg);
  cursor: pointer;
}
.catalog__tab:hover {
  border-color: #c70101;
  color: #c70101;
  box-shadow: inset 15px 0 0 #c70101;
}
.catalog__tab--active {
  background: #c70101;
  border-color: #c70101;
  color: #fff;
}
.catalog__tab--active:hover {
  color: #fff;
}
.catalog__tab div {
  transform: skewX(25deg);
}
.catalog__content {
  margin-top: 57px;
  display: none;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.catalog__content--active {
  display: flex;
  animation: fade 0.5s ease-in;
}
@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.catalog-item {
  position: relative;
  width: 300px;
  margin: 0 5px 10px;
  max-width: 100%;
  min-height: 442px;
  border: 2px solid #E8E8E8;
  padding: 20px;
  overflow: hidden;
}
.catalog-item__wrapper {
  margin-left: -20px;
  display: flex;
  width: calc(200% + 84px);
}
.catalog-item__content {
  position: relative;
  width: 50%;
  margin: 0 20px;
  padding-bottom: 25px;
  transform: translateX(calc(-100% - 42px));
  transition: 0.5s all;
}
.catalog-item__content--active {
  transform: translateX(0%);
}
.catalog-item__list {
  position: relative;
  width: 50%;
  margin: 0 20px;
  padding: 0 0 25px 20px;
  margin-bottom: 0;
  list-style: none;
  transition: 0.5s all;
  transform: translateX(0%);
}
.catalog-item__list--active {
  transform: translateX(calc(-100% - 40px));
}
.catalog-item__list li {
  position: relative;
  font-weight: 300;
  font-size: 14px;
  line-height: 21px;
  color: #555555;
  margin-bottom: 10px;
}
.catalog-item__list li::before {
  content: "";
  position: absolute;
  top: 7px;
  left: -15px;
  width: 5px;
  height: 5px;
  background-color: #c70101;
}
.catalog-item__img img {
  display: block;
  max-width: 100%;
  height: 200px;
  margin: 0 auto;
}
.catalog-item__title {
  margin-top: 15px;
  font-weight: 700;
  font-size: 16px;
  text-align: center;
  color: #0D0D0D;
}
.catalog-item__descr {
  margin-top: 15px;
  font-weight: 300;
  font-size: 14px;
  text-align: center;
  color: #0D0D0D;
}
.catalog-item .catalog-item__link, .catalog-item .catalog-item__back {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 10px;
  display: block;
  text-align: center;
  text-decoration: underline;
  color: #c70101;
}
.catalog-item .catalog-item__link:hover, .catalog-item .catalog-item__back:hover {
  color: #c70101;
}
.catalog-item hr {
  border: none;
  background-color: #e8e8e8;
  height: 1px;
  margin: 17px 0;
}
.catalog-item__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.catalog-item__prices {
  margin-top: 7px;
}
.catalog-item__old-price {
  font-weight: 300;
  font-size: 14px;
  text-decoration: line-through;
  color: #555555;
}
.catalog-item__price {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #050505;
}

@media (max-width: 991.98px) {
  .container {
    max-width: 720px;
  }
  .promo__header {
    font-size: 36px;
    line-height: 39px;
  }
  .promo__subheader {
    font-size: 20px;
    line-height: 24px;
  }
  .promo__icons {
    width: 100%;
    margin-right: 0;
    justify-content: space-around;
  }
  .adventeges__item {
    width: 31%;
  }
  .title--left {
    font-size: 30px;
    line-height: 45px;
  }
  .catalog-item {
    flex: 1 0 45%;
  }
}
@media (max-width: 767.98px) {
  .container {
    max-width: 540px;
  }
  .d-sm-none {
    display: none;
  }
  .header__item {
    flex: 0 0 50%;
    max-width: 50%;
  }
  .header__logo {
    max-width: 200px;
    width: 100%;
  }
  .header__phone {
    font-size: 16px;
    margin-bottom: 2px;
  }
  .header .button {
    height: auto;
    padding: 4px 0;
    width: 150px;
  }
  .promo__header {
    font-size: 30px;
    line-height: 33px;
  }
  .promo__subheader {
    font-size: 18px;
    line-height: 22px;
  }
  .promo__icons {
    width: 100%;
    margin-right: 0;
    justify-content: space-around;
  }
  .adventeges__items {
    justify-content: center;
    flex-wrap: wrap;
  }
  .adventeges__item {
    width: 50%;
    padding-left: 15px;
    padding-right: 15px;
  }
  .title {
    font-size: 26px;
  }
  .title--left {
    line-height: 35px;
  }
  .consultation {
    min-height: 500px;
    padding: 60px 0 68px;
  }
  .consultation__descr {
    font-size: 18px;
    line-height: 24px;
  }
  .catalog__tab {
    font-size: 14px;
  }
  .catalog-item {
    flex: 1 0 auto;
  }
}
@media (max-width: 575.98px) {
  .container {
    max-width: 100%;
  }
  .header {
    margin-left: -15px;
    margin-right: -15px;
  }
  .header__item {
    padding-left: 15px;
    padding-right: 15px;
  }
  .header__phone {
    font-size: 12px;
  }
  .header .button {
    font-size: 10px;
    max-width: 100%;
  }
  .promo__wrapper {
    margin-top: 80px;
    text-align: center;
    align-items: center;
  }
  .promo__icons {
    margin: 50px 0 55px;
  }
  .promo__icon {
    font-size: 12px;
    line-height: 15px;
  }
  .promo__link {
    font-size: 14px;
  }
  .adventeges__item {
    max-width: 300px;
    width: 100%;
    padding-left: 0;
    padding-right: 0;
  }
  .title {
    font-size: 20px;
    line-height: 30px;
  }
  .form {
    margin-left: auto;
    margin-right: auto;
  }
  .form input {
    opacity: 0.7;
  }
  .carousel {
    padding: 65px 0;
  }
  .catalog__tab div, .catalog__tab {
    transform: unset;
  }
  .catalog__tabs {
    margin-top: 30px;
    flex-wrap: wrap;
  }
  .catalog__tab {
    padding: 7px 14px;
    margin-bottom: 10px;
    width: auto;
    height: auto;
    border-radius: 5px;
  }
  .catalog__content {
    margin-top: 40px;
  }
}